import disney from './Furniture images/disney.svg'
import deezer  from './Furniture images/deezer.svg'
import verizon from './Furniture images/verizon.svg'
import texas from './Furniture images/texas.svg'

const Section3 = () => {
  return (
    <div className='sectionC'>
        <h2 className='partners'>Our Partners</h2>
        <h5><small>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quo sed soluta ullam quibusdam iusto provident, quos qui libero ratione exercitationem?</small></h5> 
        <div className='container one'>
            <div className='row'>
                <div className='col-4'>
                <img src={spotify} alt="" className='spotify' width={90} />
                </div>
                <div className='col-4'>
                    <img src={dell} alt="" className='dell' width={90} />
                </div>
                <div className='col-4'>
                    <img src={cisco} alt="" className='cisco' width={90} />
                </div>
            </div>
        </div>

        <div className='container two'>
            <div className='row'>
                <div className='col-4'>
                <img src={disney} alt="" className='disney' width={90} />
                </div>
                <div className='col-4'>
                <img src={deezer} alt="" className='deezer' width={90} />
                </div>
                <div className='col-4'>
                <img src= {verizon} alt="" className='verizon' width={90} />
                </div>
            </div>
        </div>

        <div className='container three'>
            <div className='row'>
                <div className='col-4'>
                </div>
                <div className='col-4'>
                <img src={texas} alt="" className='texas' width={90} />
                </div>
                <div className='col-4'>
                </div>
            </div>
        </div>



    </div>
  )
}

export default Section3