import React from 'react'
import App from './Furniture images/app.svg'
import Couch from './Furniture images/couch.png'
const Section1 = () => {
  return (
    <div className='sectionA'>
        <div className='living'>
        <img src={App} alt="" width={150} />
        </div>
        <div className='couch'>
            <img src={Couch} alt="" width={250} />
        </div>


    </div>
  )
}

export default Section1