import React from 'react';
import Homestart from './Furniture images/logo.svg'


const Navbar = () => {
  return (
    <div className='navbar'>
        <nav class="navbar navbar-expand-lg">
  <div class="container-fluid">
    <a class="navbar-brand" href="#"> <img src={Homestart} alt="" className='homestartimg' width={150}  /></a>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav">
        <li class="nav-item">
          <a class="nav-link" href="#" className='navhome'>HOME</a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#" className='navcontact'>CONTACT</a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#" className='login'>LOGIN</a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href='#' className='sign-up'>SIGNUP</a>
        </li>
      </ul>
    </div>
  </div>
</nav>

</div>
  )
}

export default Navbar