import React from 'react'
import download from './Furniture images/download.svg'
import key from './Furniture images/key.svg'
import payment from './Furniture images/payment.svg'
import anytime from './Furniture images/anytime.svg'
import dots from './Furniture images/dots.svg'

const Section2 = () => {
  return (
    <div className='sectionB'>
        <div className='download1'>
            <img src={download} alt="" width={30} /> <span>Download The App</span>
        </div>
        <div className='B-one'>
        <div className='safe'>
            <img src={key} alt="" className='lokey' />
            <h4 className='saf'>Safe</h4>
            <h6 className='losafe'> <small> Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsam, expedita. Ea, nobis!</small></h6>
        </div>

        <div className='payments'>
            <img src={payment} alt="" className='lopayments' />
            <h4 className='easy'>Easy Payments</h4>
            <h6 className='lopay'> <small> Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsam, expedita. Ea, nobis!</small></h6>
        </div>

        <div className='anytime'>
        <img src={anytime} alt="" className='loanytime' />
        <h4 className='any'>Anytime</h4>
        <h6 className='loany'> <small> Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsam, expedita. Ea, nobis!</small></h6>
        </div>
        </div>
        <div className='dot1'>
          <img src={dots} alt="" />
        </div>
        <div className='dot2'>
          <img src={dots} alt="" />
        </div>

    </div>
  )
}

export default Section2